import classes from "./Criteria.module.css";

export default function CriteriaSalary() {
  return (
    <table className={`${classes.criteriaTable} ${classes.solary}`}>
      <thead>
        <tr style={{ color: "darkblue", fontWeight: "bold" }}>
          <td style={{ height: "37px" }}>  </td>
          <td> /</td>
          <td>  </td>
          <td> </td>
          <td> </td>
        </tr>
      </thead>
      <tbody>
        <tr style={{ width: "100px" }}>
          <td className={classes.helper1}></td>
          <td>50</td>
          <td>-</td>
          <td rowSpan="3">+ 0.5     2- </td>
          <td rowSpan="3">   </td>
        </tr>
        <tr>
          <td className={classes.helper2}></td>
          <td>100</td>
          <td rowSpan="2">+ 15   </td>
        </tr>
        <tr>
          <td className={classes.moderator}></td>
          <td>150</td>
        </tr>
      </tbody>
    </table>
  );
}
