import React from "react";
import Selector from "./Selector";

const actions = [
  { value: "appointment", label: "" },
  { value: "logs", label: "" },
  { value: "reportEdit", label: "  " },
];

export default function SelectAction({ onChange }) {
  return (
    <Selector
      options={actions}
      defaultValue={actions[0]}
      onChange={({ value }) => onChange(value)}
    />
  );
}
