import React from "react";
import Selector from "./Selector";

export const roles = [
  {
    name: "user",
    lvl: 1,
    viewName: "",
  },
  {
    name: "helper1",
    lvl: 2,
    viewName: "",
  },
  {
    name: "helper2",
    lvl: 3,
    viewName: "",
  },
  {
    name: "mod",
    lvl: 4,
    viewName: "",
  },
  {
    name: "st",
    lvl: 5,
    viewName: " ",
  },
  {
    name: "gm",
    lvl: 6,
    viewName: " ",
  },
  {
    name: "curator",
    lvl: 7,
    viewName: "",
  },
  {
    name: "admin",
    lvl: 8,
    viewName: "",
  },
];

const actions = [
  { value: "user", label: "" },
  { value: "helper1", label: "" },
  { value: "helper2", label: "" },
  { value: "mod", label: "" },
  { value: "st", label: " " },
  { value: "gm", label: " " },
  { value: "curator", label: "" },
  { value: "admin", label: "" },
];

export default function SelectRole({ value, setRole, maxLvl }) {
  return (
    <Selector
      options={actions.filter((_, index) => {
        return index < maxLvl - 1;
      })}
      value={actions[value]}
      onChange={(changeValue) => {
        setRole(actions.indexOf(changeValue) + 1);
      }}
    />
  );
}
